import React, { useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { AutorizationContext, StoreContext } from '../context/context';

const User = () => {
	const navigate = useNavigate(); //  
	const { setIsAuth } = useContext(AutorizationContext); //   
	const { setTheme } = useContext(StoreContext); //   
	const handleClick = () => {
		localStorage.removeItem('Auth');
		localStorage.removeItem('User');
		localStorage.removeItem('defaultTheme');
		localStorage.removeItem('theme');
		setIsAuth(false)
		setTheme('')
		navigate('/login');
	} //     localStorage     
	return (
		<span className='user' onClick={handleClick}>{localStorage.getItem(`User`)}</span>
	);
}

export default User;