// 
import React, { useState, useEffect, useContext } from 'react';
import style from './Random.module.css';
import Random from './Random';
import Block from '../../../components/UI/Block/Block';
import { StoreContext } from '../../../context/context';
/////////////////////////////////////////////////////

const RandomBlock = () => {
	// 
	const { gameParametres, setGameParametres, isGameRolesChanged, defaultRoles, player, theme } = useContext(StoreContext); //     
	const [randomSwitch, setRandomSwitch] = useState(true); //      
	const [roleData, setRoleData] = useState(JSON.parse(localStorage.getItem('roleData')) || []); //  
	const [slotData, setSlotData] = useState(JSON.parse(localStorage.getItem('slotData')) || []) //  
	// /////////////////////////////////////////////////////////

	// 
	const getInitialSlotData = (players) => {
		const initialSlots = [];
		for (let i = 1; i <= players; i++) {
			initialSlots.push({ id: i, slot: i });
		}
		return initialSlots;
	}; //      - 

	const handleRoleClick = (newData) => {
		setRoleData(newData);
		const newPlayers = newData.map((role) => ({
			...player,
			id: role.id,
			role: role.role,
		})); //     

		setGameParametres((prevParametres) => ({
			...prevParametres,
			players: newPlayers,
		})); //      gameParametres

		localStorage.setItem('roleData', JSON.stringify(newData)); //  (    )   localStorage
	}; //  (    )    (     )    (gameParametres),

	const handleSlotClick = (newData) => {
		setSlotData(newData);
		localStorage.setItem('slotData', JSON.stringify(newData));
	}; //  (    )    slotData

	useEffect(() => {
		if (isGameRolesChanged) {
			setRoleData(defaultRoles); //   
			setSlotData(getInitialSlotData(gameParametres.valuePlayers)); //   
			localStorage.setItem('roleData', JSON.stringify(defaultRoles)); //     localStorage
		}
	}, [defaultRoles, isGameRolesChanged]); //   /   roleData/slotData    -  (        (      localStorage))

	useEffect(() => {
		localStorage.setItem('roleData', JSON.stringify(roleData));
		localStorage.setItem('slotData', JSON.stringify(slotData));
	}, [roleData, slotData]); //      (gameParametres.roles).        localStorage

	useEffect(() => {
		const newPlayers = roleData.map((role) => ({
			...player,
			id: role.id,
			role: role.role,
		}));

		setGameParametres((prevParametres) => ({
			...prevParametres,
			players: newPlayers,
		}));
	}, [roleData]); //      (gameParametres.players).
	///////////////////////////////////////

	//  
	return (
		<div className={style.random}>
			<Block //     ,  styles    
				className={style.randomBlock}
			>
				<input //   
					type="button"
					value={randomSwitch ? ' ' : ' '}
					onClick={() => setRandomSwitch((prevState) => !prevState)}
					className={`${style.mainBtn} ${theme} btn`}
				/>
				<div className={style.randomPlayers}> {/*  */}
					{randomSwitch ? (
						<Random data={roleData} handleClick={handleRoleClick} randomSwitch={randomSwitch} type={'role'} text="" /> //  
					) : (
						<Random data={slotData} handleClick={handleSlotClick} randomSwitch={randomSwitch} type={'slot'} text="" /> //  
					)}
				</div>
			</Block>
		</div>
	);
	// /////////////////////////////////////
};

export default RandomBlock;