// 
import React, { useState, useEffect, useContext } from 'react';
import style from './Switch2.module.css';
import { StoreContext } from '../../../../context/context';
import InfoBlock from '../../InfoBlock/InfoBlock';
/////////////////////////////////////////////////////

const Switch2 = ({ state, changeState }) => {

	// 
	const [switch2, setSwitch2] = useState(state); //   (true/false)
	const { theme } = useContext(StoreContext); //     
	// /////////////////////////////////////////////////////////

	// 
	useEffect(() => {
		changeState(switch2);
	}, [switch2, changeState]); //        

	const handleClick = () => {
		setSwitch2(prevState => !prevState);
	}; //        
	///////////////////////////////////////
	const stateArr = {
		allTrue: true,
		OFF: !switch2,
	}
	//  
	return (
		<div
			className={`${style.switch2} ${theme} switch2 ${switch2 ? 'switchON' : 'switchOFF'}`} //  
			onClick={handleClick}
		>
			<InfoBlock classes={stateArr}>
				<div
					className={`${style.circle} ${theme} switchCircle`} //  
				></div>
			</InfoBlock>
		</div>

	);
	// /////////////////////////////////////
};

export default Switch2;