// 
import React, { useContext, useEffect } from 'react';
import { Route, Routes, useNavigate } from 'react-router-dom';
import { privateRoutes, publicRoutes } from '../router/routes';
import { StoreContext, AppRouterContext, AutorizationContext } from '../context/context';
/////////////////////////////////////////////////////

const AppRouter = () => {
	// 
	const { setPageNow } = useContext(StoreContext); //     
	const { isAuth, setIsAuth } = useContext(AutorizationContext); //   
	/////////////////////////////////////////////////////

	// 
	const navigate = useNavigate();
	useEffect(() => {
		const auth = JSON.parse(localStorage.getItem('Auth'));
		const storedPageNow = localStorage.getItem('pageNow') ? localStorage.getItem('pageNow') : '/start';
		if (auth) {
			setIsAuth(true);
			if (storedPageNow === '/start') {
				setPageNow('/start');
				navigate('/start');
			} else if (storedPageNow === '/game') {
				setPageNow('/game');
				navigate('/game');
			} else if (storedPageNow === '/results') {
				setPageNow('/results');
				navigate('/results');
			}
		} else {
			navigate('/login');
		}
	}, [navigate, isAuth]); //    ,     

	const changePage = (link) => {
		localStorage.setItem('pageNow', link);
		setPageNow(link);
		navigate(link);
	}; //     
	/////////////////////////////////////////////////////

	//  
	return (
		<AppRouterContext.Provider // useContex       
			value={{
				changePage //  
			}} //  value   ,       ( : 1  - 1      )
		>
			<Routes>
				{isAuth ? (
					<>
						{privateRoutes.map(route => ( //    ,      -     routes.js privateRoutes
							<Route
								path={route.path}
								element={route.element}
								exact={route.exact}
								key={route.path}
							/>
						))}
					</>
				) : (
					<>
						{publicRoutes.map(route => ( //    ,     -     routes.js publicRoutes
							<Route
								path={route.path}
								element={route.element}
								exact={route.exact}
								key={route.path}
							/>
						))}
					</>
				)}
			</Routes>
		</AppRouterContext.Provider>

	);
	// /////////////////////////////////////
}

export default AppRouter;