// 
import React from 'react';
import style from './ModalWinner.module.css';
import MyModal from '../../UI/MyModal/MyModal';
import InfoBlock from '../../UI/InfoBlock/InfoBlock';
//////////////////////////////////////////


const ModalWinner = ({ visible, setVisible, gameParametres, setGameParametres, timer, changePage }) => {

	//       
	const currentDate = new Date();
	const day = String(currentDate.getDate()).padStart(2, '0');
	const month = String(currentDate.getMonth() + 1).padStart(2, '0');
	const year = currentDate.getFullYear();
	const hours = String(currentDate.getHours()).padStart(2, '0');
	const minutes = String(currentDate.getMinutes()).padStart(2, '0');
	const formattedDate = `${day}.${month}.${year} ${hours}:${minutes}`;
	/////////////////////////////////

	const winnerClick = (winner) => {
		const updatedGameParametres = {
			...gameParametres,
			winner: winner,
			time: timer,
			date: formattedDate,
		}; //   

		setGameParametres(updatedGameParametres); //  gameParametres

		localStorage.setItem('startGame', JSON.stringify(false)); //  
		localStorage.removeItem('timer'); //     localStorage
		localStorage.removeItem('timeGame'); //     localStorage
		localStorage.setItem('gameParametres', JSON.stringify(updatedGameParametres)); //   

		changePage('/results'); //   Results
	}; //   

	//  
	return (
		<MyModal visible={visible} setVisible={setVisible}>
			<InfoBlock>
				<div className={style.modalWinner}>
					<h1> :</h1>
					<div className={style.winnerBtns}>
						<button className={style.btn} onClick={() => winnerClick(['', ''])}><span className='roleSpan'><span className='smile'></span></span></button>
						<button className={style.btn} onClick={() => winnerClick(['', ''])}><span className='roleSpan'> <span className='smile'></span></span></button>
						{gameParametres.roles.maniac && gameParametres.roles.maniac.value && <button className={style.btn} onClick={() => winnerClick(['', ''])}><span className='roleSpan'> <span className='smile'></span></span></button>}

					</div>
				</div>
			</InfoBlock>
		</MyModal>
	);
	//////////////////////////////////////////
};

export default ModalWinner;